	color:#414141;

	margin: 0 0 30px 0;
	line-height:1.5;
	-webkit-font-smoothing: antialiased;
}

h1 {
	font-size: 36px;
	margin: 0 0 44px 0;
}

h2 {
	font-size: 30px;
	margin: 0 0 32px 0;
}

h3 {
	font-size: 24px;
	letter-spacing: normal;
	margin: 0 0 32px 0;
}

h4 {
	font-size: 18px;
	letter-spacing: normal;
	margin: 0 0 14px 0;
}

h5 {
	font-size: 14px;
	letter-spacing: normal;
	margin: 0 0 20px 0;
}

h6 {
	font-size: 12px;
	letter-spacing: normal;
