/* FORM ELEMENTS */
.radio+.radio, 
.checkbox+.checkbox {
	margin-top:1px;
}
/**/
/* radios and checkboxes */
/**/
.radio,
.checkbox {
	display:inline-block;
	margin: 0 15px 3px 0;
	padding-left: 27px;
	font-size: 15px;
	line-height: 27px;
	color: #404040;
	cursor: pointer;
}
section.dark .radio,
section.dark .checkbox {
	color: #eee;
}
.radio input,
.checkbox input {
	position: absolute;
	left: -9999px;
}
.radio i,
.checkbox i {
	position: absolute;
	top: 5px;
	left: 0;
	display: block;
	width: 19px;
	height: 19px;
	outline: none;
	border-width: 2px;
	border-style: solid;
	border-color:rgba(0,0,0,0.3);
	background: rgba(255,255,255,0.3);
}
section.dark .radio i,
section.dark .checkbox i {
	border-color:rgba(255,255,255,0.3);
	background: transparent;
}
.radio i {
	-webkit-border-radius: 50% !important;
	   -moz-border-radius: 50% !important;
			border-radius: 50% !important;
}
.radio input + i:after,
.checkbox input + i:after {
	position: absolute;
	opacity: 0;
	transition: opacity 0.1s;
	-o-transition: opacity 0.1s;
	-ms-transition: opacity 0.1s;
	-moz-transition: opacity 0.1s;
	-webkit-transition: opacity 0.1s;
}
.radio input + i:after {
	content: '';
	top: 5px;
	left: 5px;
	width: 5px;
	height: 5px;
	border-radius: 50%;
}
.checkbox input + i:after {
	content: '\f00c';
	top: 0;
	left: 0px;
	width: 15px;
	height: 15px;
	font: normal 12px/16px FontAwesome;
	text-align: center;
}
.radio input:checked + i:after,
.checkbox input:checked + i:after {
	opacity: 1;
}
.inline-group {
	margin: 0 -30px -4px 0;
}
.inline-group:after {
	content: '';
	display: table;
	clear: both;
}
.inline-group .radio,
.inline-group .checkbox {
	float: left;
	margin-right: 30px;
}
.inline-group .radio:last-child,
.inline-group .checkbox:last-child {
	margin-bottom: 4px;
}

