    border-bottom: 1px dashed #ddd;
    min-height: 10px;
    overflow: auto;
}

.md-editor>textarea {
    font-size: 14px;
    outline: 0;
    outline: thin dotted \9;
    margin: 0;
    display: block;
    padding: 0;
    width: 100%;
    border: 0;
    border-top: 1px dashed #ddd;
    border-bottom: 1px dashed #ddd;
    border-radius: 0;
    box-shadow: none;
    background: #fafafa;
}

.md-editor>textarea:focus {
    box-shadow: none;
    background: #fff;
}

.md-editor.active {
    border-color: #999;
    outline: 0;
