/* fix the issue on bootstrap flipped css */
@media only screen and (max-width: 480px) {
    .mobile-block {
        float: none !important;
    }
}

.controlls-over .owl-controls .owl-next, .controlls-over .owl-controls .owl-prev {
    padding-top: 10px;
}

