.has-error .form-control {
    border-color: #a94442 !important;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075) !important;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075) !important;
}

    .has-error .form-control:focus {
        border-color: #843534 !important;
        -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 6px #ce8483 !important;
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 6px #ce8483 !important;
    }

/* Fix the issue with rtl mega menu*/
#topNav #topMain > li.mega-menu div.row div {
    border-left: none;
    margin-left: 0;
    border-right: rgba(0,0,0,0.1) 1px solid;
    margin-right: -1px;
}

#topNav #topMain > li.mega-menu div.row > div:first-child {
    border-right: 0;
}

/* omit the border for genre cells in mega menu */
#topNav #topMain > li.mega-menu div.row > div:nth-child(2) {
    border-right: 0 !important;
}

.truncate {
    white-space: nowrap;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
}


.discount-form .voucher {
    padding: 5px;
    border-radius: 5px;
    margin-right: 10px;
    margin-left: 10px;
}

#loading-bar .bar {
    background: #fff;
}

#loading-bar .peg {
    -moz-box-shadow: #fff 1px 0 6px 1px;
    -ms-box-shadow: #fff 1px 0 6px 1px;
    -webkit-box-shadow: #fff 1px 0 6px 1px;
    box-shadow: #fff 1px 0 6px 1px;
}

#loading-bar-spinner .spinner-icon {
    border-top-color: #fff;
    border-left-color: #fff;
}

@media only screen and (max-width: 767px) {
    div.side-nav .side-nav-head button {
        left: 0;
        right: auto;
    }

    div.side-nav .side-nav-head > h4 {
        padding-right: 15px;
    }

    #slider div.slider-featured-text h2 {
        line-height: 30px;
    }
}


