/**
	DO NOT USE IT IF YOU DON'T NEED IT!

	This file is not used - you can use it on your custom.css 
	if you want to rewrite the fonts.
	
	
	Three google fonts are used on Smarty:
	- Open Sans (default font)
	- Raleway (used for numbers)
	- Lato (used to highlight the text using font-weight:300)
	
	* Please note: all files starting with "_" (underscore), are not used by default!
 ** ***************************** **/



/** essentials.css 
 *********************** **/
.countTo-sm,
.countTo-md,
.countTo-lg,
.countdown,
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: IRANSans, 'Raleway','Open Sans',Arial,Helvetica,sans-serif;
}

.ribbon-inner {
    font-family: IRANSans, "Open Sans",Helvetica,Arial,sans-serif;
}

div.mega-price-table .pricing-head small {
    font-family: IRANSans, 'Open Sans', Arial, Hevletica, sans-serif;
}

.datepicker.dropdown-menu {
    font-family: IRANSans, "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.time_pick .time,
.time_pick .mins,
.time_pick .meridian,
.mfp-close,
.price-clean {
    font-family: IRANSans, Arial, Helvetica, sans-serif;
}

.daterangepicker .calendar th, .daterangepicker .calendar td {
    font-family: IRANSans, 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

.flex-caption {
    font-family: IRANSans, 'Lato',Arial,Helvetica,sans-serif;
}

.switch > .switch-label {
    font-family: IRANSans, Helvetica, Arial, sans-serif;
}













/** layout.css 
 *********************** **/
body {
    font-family: IRANSans, 'Open Sans',Arial,Helvetica,sans-serif;
}

small {
    font-family: IRANSans, 'Lato', sans-serif;
}

/* fonts */
.font-open-sans {
    font-family: IRANSans, 'Open Sans',Arial,Helvetica,sans-serif !important;
}

.timeline .timeline-entry,
.font-lato {
    font-family: IRANSans, 'Lato',Arial,Helvetica,sans-serif !important;
}

.font-raleway {
    font-family: IRANSans, 'Raleway',Arial,Helvetica,sans-serif !important;
}
/* elastic slider */
.ei-title h3 {
    font-family: IRANSans, 'Open Sans', sans-serif;
}

.maintenance,
.timeline .timeline-entry > span,
#footer ul.footer-list li small,
.comments small,
h1.blog-post-title,
.blog-post-item h2 {
    font-family: IRANSans, 'Open Sans',Arial,Helvetica,sans-serif;
}

.demo-icon
    {
      font-family: "fontello";
      font-style: normal;
      font-weight: normal;
      speak: none;
     
      display: inline-block;
      text-decoration: inherit;
      width: 1em;
      margin-right: .2em;
      text-align: center;
      /* opacity: .8; */
     
      /* For safety - reset parent styles, that can break glyph codes*/
      font-variant: normal;
      text-transform: none;
     
      /* fix buttons height, for twitter bootstrap */
      line-height: 1em;
     
      /* Animation center compensation - margins should be symmetric */
      /* remove if not needed */
      margin-left: .2em;
     
      /* You can be more comfortable with increased icons size */
      /* font-size: 120%; */
     
      /* Font smoothing. That was taken from TWBS */
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
     
      /* Uncomment for 3D effect */
      /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
    }

.cfont {
    display: inline-block;
    font: normal normal normal 14px/1 fontello;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

